'use client';

import { Fragment } from 'react';
import { Menu, Transition } from '@headlessui/react';
import { ChevronDownIcon, UserCircleIcon } from '@heroicons/react/24/outline';
import {
  UserIcon,
  Cog6ToothIcon,
  BellIcon,
  ArrowRightOnRectangleIcon,
} from '@heroicons/react/24/outline';
import Link from 'next/link';
import { useAuth } from '@/contexts/AuthContext';
import Image from 'next/image';

export default function NavbarUserMenu() {
  const { user, isAuthenticated, logout } = useAuth();
  
  if (!isAuthenticated) {
    return (
      <div className="flex gap-2">
        <Link
          href="/login"
          className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-indigo-600 hover:text-indigo-500 focus:outline-none"
        >
          تسجيل الدخول
        </Link>
        <Link
          href="/signup"
          className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
        >
          إنشاء حساب
        </Link>
      </div>
    );
  }
  
  return (
    <Menu as="div" className="relative inline-block text-right">
      <div>
        <Menu.Button className="inline-flex w-full justify-center items-center gap-x-1.5 rounded-md px-3 py-2 text-sm font-semibold text-gray-900 hover:bg-gray-100">
          <div className="flex items-center">
            <div className="flex-shrink-0 h-8 w-8 relative">
              {user.profileImage ? (
                <Image
                  className="h-full w-full rounded-full object-cover"
                  src={user.profileImage}
                  alt={user.name}
                  width={32}
                  height={32}
                />
              ) : (
                <UserCircleIcon className="h-8 w-8 text-gray-400" />
              )}
            </div>
            <div className="mr-2 hidden md:block text-right">
              <p className="text-sm font-medium text-gray-800">{user.name}</p>
              <p className="text-xs text-gray-500 truncate">
                {user.accountType === 'client' ? 'عميل' : 'مزود خدمة'}
              </p>
            </div>
          </div>
          <ChevronDownIcon className="h-5 w-5 text-gray-400" aria-hidden="true" />
        </Menu.Button>
      </div>

      <Transition
        as={Fragment}
        enter="transition ease-out duration-100"
        enterFrom="transform opacity-0 scale-95"
        enterTo="transform opacity-100 scale-100"
        leave="transition ease-in duration-75"
        leaveFrom="transform opacity-100 scale-100"
        leaveTo="transform opacity-0 scale-95"
      >
        <Menu.Items className="absolute left-0 z-10 mt-2 w-56 origin-top-left rounded-md bg-white shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none">
          <div className="py-1">
            <div className="px-4 py-3">
              <p className="text-sm">{user.name}</p>
              <p className="text-sm font-medium text-gray-900 truncate">{user.email}</p>
            </div>
            
            <hr className="border-gray-200" />
            
            <Menu.Item>
              {({ active }) => (
                <Link
                  href="/dashboard"
                  className={`${
                    active ? 'bg-gray-100 text-gray-900' : 'text-gray-700'
                  } flex px-4 py-2 text-sm w-full items-center`}
                >
                  <UserIcon className="ml-3 h-5 w-5 text-gray-500" />
                  الملف الشخصي
                </Link>
              )}
            </Menu.Item>
            
            <Menu.Item>
              {({ active }) => (
                <Link
                  href="/dashboard/settings"
                  className={`${
                    active ? 'bg-gray-100 text-gray-900' : 'text-gray-700'
                  } flex px-4 py-2 text-sm w-full items-center`}
                >
                  <Cog6ToothIcon className="ml-3 h-5 w-5 text-gray-500" />
                  الإعدادات
                </Link>
              )}
            </Menu.Item>
            
            <Menu.Item>
              {({ active }) => (
                <Link
                  href="/dashboard/notifications"
                  className={`${
                    active ? 'bg-gray-100 text-gray-900' : 'text-gray-700'
                  } flex px-4 py-2 text-sm w-full items-center`}
                >
                  <BellIcon className="ml-3 h-5 w-5 text-gray-500" />
                  الإشعارات
                </Link>
              )}
            </Menu.Item>
            
            <hr className="border-gray-200" />
            
            <Menu.Item>
              {({ active }) => (
                <button
                  type="button"
                  onClick={logout}
                  className={`${
                    active ? 'bg-gray-100 text-gray-900' : 'text-gray-700'
                  } flex px-4 py-2 text-sm w-full items-center`}
                >
                  <ArrowRightOnRectangleIcon className="ml-3 h-5 w-5 text-gray-500" />
                  تسجيل الخروج
                </button>
              )}
            </Menu.Item>
          </div>
        </Menu.Items>
      </Transition>
    </Menu>
  );
} 